export default {
	data() {
		return {
			key: '',
			star: [
				{
					active: 1
				},
				{
					active: 0
				},
				{
					active: 0
				},
				{
					active: 0
				},
				{
					active: 0
				}
			],
			content: '',
			id: ''
		};
	},
	methods: {
		activeStar(index) {
			for (let i = 0; i < this.star.length; i++) {
				this.star[i].active = 0;
			}
			for (let i = 0; i < index + 1; i++) {
				this.star[i].active = 1;
			}
		},
		sub() {
			let actives = [];
			this.star.forEach(res => {
				if (res.active === 1) {
					actives.push(res);
				}
			});
			let stars = actives.length;
			let text = '感谢您的支持！';
			if (stars <= 3) {
				text = '您确定要给我打这么低的分吗？';
			}
			uni.showModal({
				title: '确定要提交吗？',
				content: text,
				success: res => {
					if (res.confirm) {
						console.log('用户点击确定');
						let data = {
							point: stars,
							id: this.id,
							content: this.content
						};
						
					}
				}
			});
		}
	},
	onLoad() {
		this.id = this.$Route.query.id;
	}
};
